<template>


    <v-progress-circular
            :rotate="360"
            :size="50"
            :width="5"
            :value="percentUsed"
            :color="getColor(percentUsed)"
    >
        {{ percentUsed }}%
    </v-progress-circular>

</template>

<script>
  export default {
    name: "gauge",
    components: {},
    props:
        {
          'percentUsed': {
            default: 0,
            type: Number
          }
        },
    data() {
      return {}
    },
    created() {

    },
    methods: {
      getColor(percent) {
        if (percent >= 100) {
          return 'red'
        } else if (percent < 100 && percent >= 75) {
          return 'yellow'
        }
        return 'teal'
      }
    }
  }
</script>

<style scoped>

</style>


