import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Account from '../components/pages/Account'
import Devices from '../components/pages/Devices'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
          {
      path: '/account',
      name: 'Account',
      component: Account
    },
    {
      path: '/devices',
      name: 'Devices',
      component: Devices
    }
  ]
})



