<template>
    <v-navigation-drawer
            persistent
            :mini-variant="miniVariant"
            :clipped="clipped"
            :temporary="temporary"
            @toggle="$store.state.sideMenu.active = !$store.state.sideMenu.active"
            v-model="$store.state.sideMenu.active"
            enable-resize-watcher
            fixed
            app
    >


        <v-layout
        >
            <v-flex>
                <v-card class="elevation-6" height="160px">
                    <v-toolbar dark class="toolbar">
                        <v-toolbar-title>Marcus Taylor</v-toolbar-title>
                        <v-spacer></v-spacer>
                        <img src="../../assets/logo-uc.png" width="40px"/>
                    </v-toolbar>
                    <v-card-text class="mb-2">
                        <v-layout row class="mb-1">
                            <v-flex>
                                marcus.taylor@cyberware.ltd.uk
                            </v-flex>
                        </v-layout>
                        <v-layout row>
                            <v-flex class="pa-1" xs3>
                                <gauge :percent-used="25"/>
                            </v-flex>
                            <v-flex class="pt-2 teal--text">
                                <div>
                                    <strong>
                                        1.5G / 5G Used
                                    </strong>
                                </div>
                                <div>
                                    15 days remaining
                                </div>
                            </v-flex>
                        </v-layout>
                    </v-card-text>

                </v-card>
            </v-flex>
        </v-layout>

        <side-menu-items :drawer="drawer"/>
    </v-navigation-drawer>
</template>
<script>
  import SideMenuItems from "./SideMenuItems";
  import Gauge from "./Gauge";

  export default {
    name: 'side-drawer',
    components: {Gauge, SideMenuItems},
    props: {
      clipped: {},
      temporary: {},
      drawer: {},
      miniVariant: {}
    }
  }
</script>


