</script>

<style scoped>
    .header-card {
        /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#42aa22+0,356347+100 */
        background: #42aa22; /* Old browsers */
        background: -moz-linear-gradient(top, #42aa22 0%, #F4D03F 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, #42aa22 0%, #F4D03F 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, #42aa22 0%, #F4D03F 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#42aa22', endColorstr='#356347', GradientType=0); /* IE6-9 */

    }

</style>


