<template>
    <v-list>
        <v-list-tile
                value="true"
                v-for="(item, i) in $store.state.sideMenu.items"
                :key="i"
                :to="item.path"
        >
            <v-list-tile-action>
                <v-list-tile-avatar>
                    <v-icon>{{ item.icon }}</v-icon>
                </v-list-tile-avatar>
            </v-list-tile-action>
            <v-list-tile-content>
                <v-list-tile-title class="black--text">{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
        </v-list-tile>
    </v-list>
</template>
<script>
  export default {
    name: 'side-menu-item',
    props: {
      drawer: {}
    },
    data() {
      return {

      }
    }
  }
</script>


