<template>
    <v-app>
        <side-drawer :clipped="clipped" :drawer="drawer" :mini-variant="miniVariant" :temporary="temporary"/>
        <v-toolbar
                app
                :clipped-left="clipped"
                class="white--text toolbar"
        >
            <v-toolbar-side-icon
                    dark
                    @click="$store.state.sideMenu.active = !$store.state.sideMenu.active"
                    :class="{'rotate-45' : $store.state.sideMenu.active}"
            >

            </v-toolbar-side-icon>
            <v-toolbar-title v-text="$store.state.header.title"></v-toolbar-title>
        </v-toolbar>
        <v-content>
            <router-view/>
        </v-content>

        <v-footer :fixed="fixed" app>
            <span>&copy; The world</span>
        </v-footer>
    </v-app>
</template>

<script>
  import SideDrawer from "./components/widgets/SideDrawer";

  export default {
    components: {SideDrawer},
    data() {
      return {
        clipped: false,
        temporary: true,
        drawer: false,
        fixed: false,
        miniVariant: false,
        title: 'UC Wireless'
      }
    },
    name: 'App'
  }
</script>

<style>
    .toolbar {
        /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#42aa22+0,356347+100 */
        background: #42aa22; /* Old browsers */
        background: -moz-linear-gradient(top, #42aa22 0%, #009688 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, #42aa22 0%,#009688 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, #42aa22 0%,#009688 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#42aa22', endColorstr='#356347',GradientType=0 ); /* IE6-9 */
    }

    .rotate-45 {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
    }
</style>



